import React, {Component} from "react";
import PropTypes from "prop-types";

import {OraButton, OraSimpleCheckbox} from "../../../../components/OraCommonComponents";
import {RequestState} from "../../../core/constants/RequestState";
import InputWithFloatingLabel from "../InputWithFloatingLabel";
import {wwtValidator} from "eshop/modules/checkout/validators";
import {post} from "eshop/flux/utils/OraApiUtils";
import ConfirmWWTPopup from "eshop/modules/magnum2/components/wwt/ConfirmWWTPopup"

import _ from "lodash";

export const prepareUrl = (url) => bsfContextPath + (typeof prefixWWW !== "undefined" ? prefixWWW : "") + url;

class WWTForm extends Component {
    static propTypes = {
        component: PropTypes.string,
        descriptions: PropTypes.shape({
            backgroundImageURL: PropTypes.string,
            headerForWWTComponent: PropTypes.string,
            getLocationFromMobile: PropTypes.string,
            locationCityPlaceholder: PropTypes.string,
            locationStreetPlaceholder: PropTypes.string,
            streetReaderOnly: PropTypes.string,
            streetTooltipContent: PropTypes.string,
            locationEstateNumberPlaceholder: PropTypes.string,
            locationApartmentNumberPlaceholder: PropTypes.string,
            submitButton: PropTypes.string,
            padLockAnchor: PropTypes.string,
            tooltipContent: PropTypes.string
        }).isRequired,
        onSubmitVerification: PropTypes.func,
        postWwtCallback: PropTypes.func,
        postWWTCallBackParameter: PropTypes.object,
        wwt: PropTypes.object,
        getZipCodeFromWWT: PropTypes.func,
        resetWWTAddress: PropTypes.func,
        getSelectedWwtAddress: PropTypes.func,
        dispatchPostWwt: PropTypes.func
    };

    state = {
        city_id: "",
        city_name: "",
        location_street_id: "",
        location_street_name: "",
        location_estate_number: "",
        location_apartment_number: "",
        isAddressWithoutStreet: false,
        isAddressWithLessThanTwoStreets: false,
        isAddressWithoutEstateNumber: false,
        city_selected: false,
        street_selected: false,
    };

    backgroundWwtComponent = {
        backgroundImage: `url(${!this.props.isModal && this.props.descriptions.backgroundImageURL})`,
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center center",
        backgroundSize: "cover"
    };

    cityAutocompleteUrl = prepareUrl("/cbs/cityauto");
    streetAutocompleteUrl = prepareUrl("/cbs/streetauto");

    constructor(props) {
        super(props);
        const wwt = props.wwt;
        this.state = {
            city_id: wwt.cityId,
            city_name: wwt.cityName,
            location_street_id: wwt.streetId,
            location_street_name: wwt.streetName,
            location_estate_number: wwt.streetNumber,
            location_apartment_number: wwt.apartmentNumber,
            available_apartments: wwt.availableApartments
        };
    }

    componentWillReceiveProps(nextProps) {
        if (this.props.wwtValidationModalState !== nextProps.wwtValidationModalState) {
            //Perhaps not the greatest workaround ever, but looks like it works.
            return;
        }
        const wwt = nextProps.wwt;
        this.setState({
            city_id: wwt.cityId,
            city_name: wwt.cityName,
            location_street_id: wwt.streetId,
            location_street_name: wwt.streetName,
            location_estate_number: wwt.streetNumber,
            location_apartment_number: wwt.apartmentNumber,
            available_apartments: wwt.availableApartments
        });

    }


    clearInputs() {
        this.setState({
            city_id: "",
            city_name: "",
            location_street_id: "",
            location_street_name: "",
            location_estate_number: "",
            location_apartment_number: "",
            isAddressWithoutEstateNumber: false,
            available_apartments: []
        });

    }

    setAddressWithoutApartmentNumber(event) {
        this.setState({isAddressWithoutApartmentNumber: event.target.checked});

    }

    componentWillMount() {
        ConfirmWWTPopup.initialize();

    }

    componentDidUpdate(prevProps, prevState) {
        if (prevState.isAddressWithLessThanTwoStreets && !this.state.isAddressWithLessThanTwoStreets) {
            this.focusOnStreet();

        }
        if (!prevState.isAddressWithLessThanTwoStreets && this.state.isAddressWithLessThanTwoStreets) {
            this.focusOnEstateNo();

        }

        if((this.props.showWWTModal&&!prevProps.showWWTModal) ||  (this.props.showWwtComponent&&!prevProps.showWwtComponent)){
            this.clearInputs();
        }

    }


    onCitySelect = (data) => {
        if (data && data.item) {
            this.setState({
                city_id: data.item.value,
                city_name: data.item.label,
                location_street_id: '',
                location_street_name: '',
                location_estate_number: '',
                location_apartment_number: '',
                available_apartments: []
            });

            OPL.UI.fire(OPL.UI.EVENTS.modules.autocomplete.querydata, {
                "city_id": data.item.value
            }, 'location_street');
            this.handleAddressWithoutStreet();
            this.focusOnStreet();
        }
    };

    focusOnStreet() {
        $(this.wrapperRef).find("#location_street").focus();
    }


    focusOnEstateNo() {
        $(this.wrapperRef).find("#location_estate_number").focus();

    }

    onStreetSelect = (data) => {

        if (data) {
            this.setState({
                location_street_id: data.item.value,
                location_street_name: data.item.label,
                available_apartments: []
            });

        }
        $("#location_estate_number:visible").focus();
    };

    equalToStateAddress = (fbbAddress) => {
        var stateAddressExtractors =
            {
                city: (a) => a.city_id,
                street: (a) => a.location_street_id,
                streetNo: (a) => a.location_estate_number,
                apartmentNo: (a) => a.location_apartment_number
            };

        var fbbAddressExtractors =
            {
                city: (a) => a.cityId,
                street: (a) => a.streetId,
                streetNo: (a) => a.line2,
                apartmentNo: (a) => a.appartmentNo
            };

        function toCanonical(addr, extractors) {
            let ret = {};
            Object.keys(extractors).forEach(extractorName => {
                let v = extractors[extractorName](addr);
                ret[extractorName] = v ? "" + v : null
            });
            return ret
        }

        let a1 = toCanonical(this.state, stateAddressExtractors);
        let a2 = toCanonical(fbbAddress, fbbAddressExtractors);

        return JSON.stringify(a1) === JSON.stringify(a2)
    };

    isWWW = () => {
        return this.props.channels && this.props.channels.sales == 'WWW'
    };

    shouldShowWWTConfirmationModal = () => {
        if (this.isWWW() && this.props.fbbServices) {
            let fbbAddresses = this.props.fbbServices.map(fb => fb.addressData);
            return fbbAddresses.some(this.equalToStateAddress)
        } else {
            return false
        }
    };

    _pickCity = (e) => {
        this.setState({
            city_name: e.value,
            city_id: "",
            location_street_id: "",
            location_street_name: ""
        });
    };

    _pickStreet = (e) => {
        this.setState({
            location_street_name: e.value,
            location_street_id: ""
        });
    };

    _onChangeEstateNumber = (e) => {
        this.setState({
            location_estate_number: e.value
        });
    };

    _onChangeApartmentNumber = (e) => {
        this.setState({
            location_apartment_number: e.value
        });
    };

    _handleSubmitWithConfirmationModal = (e) => {
        e.preventDefault();
        if (this.props.onSubmit) {
            return this.props.onSubmit(this.state.city_id, this.state.location_street_id, this.state.location_estate_number, this.state.location_apartment_number, this.state.city_name, this.state.location_street_name);

        }


        if (this.shouldShowWWTConfirmationModal()) {
            ConfirmWWTPopup.open({
                onClickConfirm: this._handleSubmit,
                onClickDecline: () => this.props.resetWWTAddress()
            })
        } else {
            this._handleSubmit()
        }
    };

    _handleSubmit = () => {
        if (wwtValidator["wwtAddressValidator"](this.state.location_estate_number).length > 0) {
            let message = wwtValidator["wwtAddressValidator"](this.state.location_estate_number)[0].message;
            this.props.openWwtValidationModal(true, message);
            return;
        } else if (wwtValidator["wwtAddressValidator"](this.state.location_apartment_number).length > 0) {
            let message = wwtValidator["wwtAddressValidator"](this.state.location_apartment_number)[0].message;
            this.props.openWwtValidationModal(true, message);
            return;
        }
        if (!this.props.onSubmitVerification()) {
            return;
        }
        if (this.state.city_id && (this.state.isAddressWithLessThanTwoStreets || this.state.location_street_id) && this.state.location_estate_number) {
            this.props.getZipCodeFromWWT(
                this.state.city_id,
                this.state.city_name,
                !!this.state.location_street_id ? this.state.location_street_id : null,
                !!this.state.location_street_name ? this.state.location_street_name : '',
                this.state.location_estate_number,
                this.state.isAddressWithoutApartmentNumber?null:this.state.location_apartment_number
            )
        }
    };

    static getAddressFromData(data) {
        return {
            postalCode: data[0].zip,
            cityId: data[0].placeId,
            streetId: data[0].streetId
        }
    }

    handleAddressWithoutStreet() {
        let data = {
            filter: {city_id: this.state.city_id},
            html: " "
        };
        post(this.streetAutocompleteUrl, JSON.stringify(data)).then(response => {
            if (response.length <= 1) {
                this.setState({isAddressWithoutStreet: response.length < 1,
                               isAddressWithLessThanTwoStreets : true
                               });
                let select = this.createSelect(response[0]);
                this.onStreetSelect(select);
            } else {
                if (this.state.isAddressWithLessThanTwoStreets) {
                    this.focusOnStreet();

                }
                this.setState({isAddressWithoutStreet: false,
                               isAddressWithLessThanTwoStreets: false
                                });
            }
        });

    }

    onCityBlur() {
        if (this.state.city_id) {
            if(!this.state.location_street_id){
                this.focusOnStreet();
            }
            return;
        }
        let data = {
            filter: "",
            html: this.state.city_name
        };
        if (!!this.state.city_name) {
            post(this.cityAutocompleteUrl, JSON.stringify(data))
                .then(response => {
                    if (response) {
                        const select = this.createSelect(response[0]);
                        this.onCitySelect(select);
                    }
                }).catch(err => console.log("City autocomplete error ", err));
        }
    }

    onStreetBlur() {
        if (this.state.location_street_id || !this.state.location_street_name) {
            return;
        }
        let data = {
            filter: {city_id: this.state.city_id},
            html: this.state.location_street_name
        };
        if (!!this.state.city_name) {
            post(this.streetAutocompleteUrl, JSON.stringify(data))
                .then(response => {
                    if (response) {
                        const select = this.createSelect(response[0]);
                        this.onStreetSelect(select);
                    }
                }).catch(err => console.log("Street autocomplete error ", err));
        }
    }

    createSelect(res) {
        if (res)
            return {
                item: {
                    label: res.name,
                    value: res.id
                }
            };
        return null;
    }

    renderCityInput() {
        return (<InputWithFloatingLabel
            id="city_id"
            value={this.state.city_name}
            required={true}
            onChange={this._pickCity}
            placeholder={this.props.descriptions.locationCityPlaceholder || "Miejscowość"}
            autocomplete={true}
            autocompleteUrl={this.cityAutocompleteUrl}
            onPick={this.onCitySelect}
            onBlur={this.onCityBlur.bind(this)}
            focusOnMount={(this.props.showWWTModal || this.props.showWwtComponent || this.props.focusCityOnMount) && (!this.state.available_apartments || !this.state.available_apartments.length)}
            showValidation={!!this.state.city_name}
            showErrors={true}
            valid={!!this.state.city_id || !this.state.city_name}
            errors={!(!!this.state.city_id)}
            overrideClass={"o-floating-label opl-floating-label-line opl-floating-label-line--white opl-autocomplete u-w-100"}
        />)
    }

    renderStreetInput() {
        return (<InputWithFloatingLabel
            id="location_street"
            value={this.state.location_street_name}
            required={true}
            onChange={this._pickStreet}
            disabled={this.state.isAddressWithLessThanTwoStreets}
            placeholder={this.props.descriptions.locationStreetPlaceholder || "Ulica"}
            autocomplete={true}
            autocompleteUrl={this.streetAutocompleteUrl}
            browserAutocomplete={"nope"}
            onPick={this.onStreetSelect.bind(this)}
            onBlur={this.onStreetBlur.bind(this)}
            showValidation={!!this.state.location_street_name}
            showErrors={true}
            valid={!!this.state.location_street_id || !this.state.location_street_name}
            errors={!(!!this.state.location_street_id)}
            overrideClass={"o-floating-label opl-floating-label-line opl-floating-label-line--white opl-autocomplete u-w-100"}
        />)
    }

    renderEstateInput() {
        return (<InputWithFloatingLabel
            id="location_estate_number"
            value={this.state.location_estate_number}
            required={true}
            onChange={this._onChangeEstateNumber}
            showValidation={!!this.state.location_estate_number}
            showErrors={true}
            valid={this.isEstateValid()}
            errors={!(!!this.state.location_estate_number)}
            placeholder={this.props.descriptions.locationEstateNumberPlaceholder || "Numer budynku"}
            browserAutocomplete={"nope"}
            overrideClass={"o-floating-label opl-floating-label-line opl-floating-label-line--white opl-autocomplete u-w-100"}
        />)
    }

    renderApartmentInput() {
        return (<InputWithFloatingLabel
                id="location_apartment_number"
                value={!!this.state.isAddressWithoutApartmentNumber?"":this.state.location_apartment_number}
                onChange={this._onChangeApartmentNumber}
                disabled={this.state.isAddressWithoutApartmentNumber}
                showValidation={(!!this.state.available_apartments && this.state.available_apartments.length > 0) || !!this.state.location_apartment_number}
                showErrors={true}
                valid={this.isApartmentValid()}
                errors={!(!!this.state.location_apartment_number)}
                placeholder={this.props.descriptions.locationApartmentNumberPlaceholder || "Numer lokalu"}
                browserAutocomplete={"nope"}
                overrideClass={"o-floating-label opl-floating-label-line opl-floating-label-line--white opl-autocomplete u-w-100"}
            />
        )
    }

    isEstateValid(){
        const reg = /^([0-9]+)(.*?)$/;
        if (reg.test(this.state.location_estate_number) === false)
            return false;
        return true;
    }

    isApartmentValid(){
        if (this.mandatoryApartmentIsInvalid()) {
            return false;
        }
        const reg = /^([0-9]+)(.*?)$/;
        if (reg.test(this.state.location_apartment_number) === false){
            return false;
        }
        return true;
    }

    mandatoryApartmentIsInvalid() {
        return !!this.state.available_apartments && this.state.available_apartments.length > 0 &&
            !this.state.location_apartment_number;
    }

    renderSubmitButton() {

        let setButtonDisable = false;
        if (this.state.isAddressWithoutStreet) {
            setButtonDisable = this.props.wwt.zipCodeRequestState === RequestState.Waiting
                            || _.some([this.state.city_id, this.state.location_estate_number], field => !field)
                            || !!this.state.location_street_name;
        } else {
            setButtonDisable =
                _.some([this.state.city_id, this.state.location_street_id, this.state.location_estate_number], field => !field);
        }
        return (<OraButton
            id="location_check_btn"
            type="primary"
            className="u-small-spacing-l u-no-padding-l u-no-padding-r u-text-center u-w-100"
            onClick={this._handleSubmitWithConfirmationModal}
            disabled={setButtonDisable}
        >
            <span dangerouslySetInnerHTML={{__html: this.props.descriptions.submitButton || "Autoryzuj"}}/>
        </OraButton>)
    }
    renderAddressWithoutApartmentNumberChecbox () {
     return(
        <div className={"u-padding-top-m"}><OraSimpleCheckbox
            checked={this.state.isAddressWithoutApartmentNumber}
            onChange={this.setAddressWithoutApartmentNumber.bind(this)}
            text={"Bez numeru lokalu"}/></div>)
    }
    renderNarrowModal() {//for pesel/address authorization methode


        return (
            <div className="opl-wwt-rwd u-padding-top-xl u-padding-l" ref={ref => this.wrapperRef = ref}>
                <div  >

                    <p className={"h5  u-spacing-l  g-white1-c"}>
                        <h6>Wpisz adres instalacyjny</h6>
                    </p>

                    <form id="wwtFormTag" name="wwtFormTag"
                          onSubmit={this._handleSubmitWithConfirmationModal.bind(this)}>
                        <div className="l-row">

                            <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 u-padding-top-m">
                                {this.renderCityInput()}

                            </div>
                            <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 u-padding-top-m ">
                                {this.renderStreetInput()}

                            </div>
                            <div
                                className={"l-col l-col-small-6 l-col-medium-3 l-col-3 u-padding-top-m"}>
                                {this.renderEstateInput()}
                            </div>
                            <div
                                className={"l-col l-col-small-6 l-col-medium-3 l-col-3 u-padding-top-m"}>
                                {this.renderApartmentInput()}
                                {this.renderAddressWithoutApartmentNumberChecbox()}
                            </div>


                            <div
                                className={"l-col l-col-small-12 l-col-medium-6 l-col-6 u-padding-top-m u-small-padding-top-m  "}>
                                {this.renderSubmitButton()}
                            </div>

                        </div>
                    </form>
                </div>

            </div>
        );
    }

    renderModal() {
        if (this.props.isNarrow) {
            return this.renderNarrowModal();
        }
        return (
            <div className="opl-wwt-rwd u-padding-top-xl u-padding-xl" ref={ref => this.wrapperRef = ref}>
                <div className="l-page-row">
                    <p className={"h3  u-spacing-l"}>
                        {this.props.descriptions.headerForWWTComponent}
                    </p>
                    <div className="u-spacing-l u-large-hide">
                        <span
                            className="g-icon g-icon--only g-icon--pin-clear g-icon--s g-white1-c u-inline-block u-spacing-right"/>
                        <a href="#" className="u-inline-block g-white1-c u-vertical-middle">
                            {this.props.descriptions.getLocationFromMobile}
                        </a>
                    </div>

                    <form id="wwtFormTag" name="wwtFormTag"
                          onSubmit={this._handleSubmitWithConfirmationModal.bind(this)}>
                        <div className="l-row">

                            <div className="l-col l-col-small-12 l-col-medium-12 l-col-4  u-spacing-l">
                                {this.props.isModal && "Wyszukaj miejscowość wpisując nazwę"}
                                {this.renderCityInput()}
                            </div>
                            <div className="l-col l-col-small-12 l-col-medium-6 l-col-4  u-spacing-l">
                                {this.props.isModal && "Wyszukaj ulicę wpisując nazwę"}
                                {this.renderStreetInput()}
                            </div>
                            <div
                                className={"l-col l-col-small-6 l-col-medium-3 l-col-2 u-spacing-l u-margin-top-l"}>
                                {this.renderEstateInput()}
                            </div>
                            <div
                                className={"l-col l-col-small-6 l-col-medium-3 l-col-2 u-spacing-l u-margin-top-l"}>
                                {this.renderApartmentInput()}
                                <div className={"u-padding-top-m"}>
                                    <OraSimpleCheckbox checked={this.state.isAddressWithoutApartmentNumber}
                                                       onChange={this.setAddressWithoutApartmentNumber.bind(this)}
                                                       text={"Bez numeru lokalu"}/></div>
                            </div>

                        </div>


                        <div className={"l-row"}>
                            <div className={"l-col l-col-3 l-col-medium-6 l-col-small-12 u-left"}>
                                <span
                                    className={"g-icon g-icon--only g-icon--padlock g-icon--s   u-inline-block u-spacing-right "}/>
                                <p className={"u-inline-block   u-vertical-middle u-text-decoration-none  "}>
                                    {this.props.descriptions.padLockAnchor}
                                </p>
                                <span
                                    data-js-module="core/modules/tooltips"
                                    data-js-options='{"trigger": "hover", "side": "top", "maxWidth": 380}'
                                >
                        <span
                            className={"o-tooltip__trigger g-icon g-icon--hint g-icon--xs-s  u-inline-block u-spacing-left  "}/>
                        <div className="o-tooltip__content">
                            <p>{this.props.descriptions.tooltipContent}</p>
                        </div>
                    </span>
                            </div>
                            <div
                                className={"l-col l-col-small-12 l-col-medium-3 l-col-2 medium-offset-by-3 u-medium-right  u-large-right u-small-margin-top-l"}>
                                {this.renderSubmitButton()}
                            </div>

                        </div>
                    </form>
                </div>

            </div>
        );
    }

    render() {
        if (this.props.isModal) {
            return this.renderModal();
        }


        return (
            <div style={this.backgroundWwtComponent} className="opl-wwt-rwd u-padding-top-xl u-padding-xl">
                <div className="l-page-row">
                    <p className={"h3  u-spacing-l  g-white1-c"}>
                        {this.props.descriptions.headerForWWTComponent}
                    </p>
                    <div className="u-spacing-l u-large-hide">
                        <span
                            className="g-icon g-icon--only g-icon--pin-clear g-icon--s g-white1-c u-inline-block u-spacing-right"/>
                        <a href="#" className="u-inline-block g-white1-c u-vertical-middle">
                            {this.props.descriptions.getLocationFromMobile}
                        </a>
                    </div>

                    <form id="wwtFormTag" name="wwtFormTag"
                          onSubmit={this._handleSubmitWithConfirmationModal.bind(this)}>
                        <div className="l-row">

                            <div className="l-col l-col-small-12 l-col-medium-12 l-col-3 u-spacing-l">
                                {this.renderCityInput()}
                            </div>
                            <div className="l-col l-col-small-12 l-col-medium-6 l-col-3 u-spacing-l ">
                                {this.renderStreetInput()}
                            </div>
                            <div
                                className={"l-col l-col-small-6 l-col-medium-3 l-col-2 u-spacing-l"}>
                                {this.renderEstateInput()}
                            </div>
                            <div
                                className={"l-col l-col-small-6 l-col-medium-3 l-col-2 u-spacing-l"}>

                                {this.renderApartmentInput()}
                            </div>

                            <div
                                className={"l-col l-col-small-12 l-col-medium-3 l-col-2 medium-offset-by-3 u-medium-right"}>
                                {this.renderSubmitButton()}
                            </div>


                        </div>


                        <div className={"l-row"}>
                            <div className={"l-col l-col-3 u-left"}>
                                <span
                                    className={"g-icon g-icon--only g-icon--padlock g-icon--s   u-inline-block u-spacing-right g-white1-c"}/>
                                <p className={"u-inline-block   u-vertical-middle u-text-decoration-none  g-white1-c"}>
                                    {this.props.descriptions.padLockAnchor}
                                </p>
                                <span
                                    data-js-module="core/modules/tooltips"
                                    data-js-options='{"trigger": "hover", "side": "top", "maxWidth": 380}'
                                >
                        <span
                            className={"o-tooltip__trigger g-icon g-icon--hint g-icon--xs-s  u-inline-block u-spacing-left  g-white1-c"}/>
                        <div className="o-tooltip__content">
                            <p>{this.props.descriptions.tooltipContent}</p>
                        </div>
                    </span>
                            </div>


                        </div>
                    </form>
                </div>

            </div>
        );
    }
}

export default WWTForm;